*,
*:after,
*:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
    font-family: 'Mont', Arial, sans-serif;
    font-size: 1em;
    font-weight: 400;
    font-style: normal;
}

main {
    min-height: 50vh;
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}

.container-flex {
    @include container-flex();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}

.row {
    @include row-flex();
    width: 100%;
}

h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
}

a {
    color: black;
    text-decoration: none;
}

button {
    cursor: pointer;
}

.d-flex { 
    display: flex;
}

.align-center {
    align-items: center;
}

.space-between {
    justify-content: space-between;
}