header {
    color: var(--main-text-color);
    background-color: var(--main-bg-color);
    font-size: 14px;
    font-weight: 300;
    height: 70px;

    .container { 
        height: 100%;
    }

    .logo {
        height: 70px;
        @include size(2);

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

.nav {
    display: flex;
    align-items: center;
    @include size(6);

    &__items {
        display: flex;     
    }
    &__item {
        padding: 10px 20px;
    }
    &__link {
        color: white;
        height: 100%;
        width: 100%;
        cursor: pointer;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            bottom: -5px;
            left: 0;
            background-color: #FFF;
            visibility: hidden;
            transform: scaleX(0);
            transition: all 0.3s ease-in-out;
        }

        &:hover:before {
            visibility: visible;
            transform: scaleX(1);
        }
    }
}

.header-widgets {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include size(4);
    margin-left: auto;

    button {
        color: white;
        background-color: transparent;
        border: 2px solid white;
        border-radius: 10px;
        padding: 10px 20px;
    }
}